import {common} from "../../utils/api/url";

import mixins from "~/utils/mixins";
import mixinsBridge from "~/utils/mixinsBridge";
import MySex from "~/components/MySex";
import {MessageError, MessageSuccess, MessageWarning} from "~/utils/utils";
export default {
  name: 'studentInfo',
  components: {MySex},
  layout: 'defaultAppScreen',
  mixins: [mixins,mixinsBridge],
  data(){
    return {
      sex: '',
      active: 6,
      serverAppList: [],
      noticeAppList: [],
      askAppList: [],
      currentEnrollYear: '',
      currentEnrollTime: '',
      collegeName: '',
      majorName: '',
      className: '',
      stuNo: '',
      headLogo: '',
      uploadFile: common.upload_file,
      uploadResult: {},
      uploadProcess: '',
      detailData: '',
      tablePayData: [],
      form: {
        id: '',
        phone: '',
        qq: '',
        email: '',
        wechat: '',
        fatherName: '',
        fatherPhone: '',
        matherName: '',
        matherPhone: '',
        address: '',
        headImg: ''
      }
    }
  },
  mounted() {

  },
  created() {
    this.initAppServer();
  },
  methods: {
    layoutInit(){

    },
    async initAppServer(){
      await this.getSessionInfo();

      this.initPay();
      this.initInfo();
      this.getUserQrcode(this.loginUserId);
      this.getUserBarcode(this.loginUserId);
    },
    initInfo(){
      let params = {
        userId: this.loginUserId
      };
      this.$axios.get(common.enroll_checkin_student_detail2, {params: params}).then(res => {
        if (res.data.data){
          this.detailData = res.data.data;
        }
      });
    },
    initPay(item){
      let params = {
        userId: this.loginUserId
      };
      this.$axios.get(common.enroll_checkin_pay_list_by_user, {params: params}).then(res => {
        if (res.data.data){
          console.log(res.data.data);
          for (let i = 0; i < res.data.data.length; i++){
            res.data.data[i]['wait_amount'] = res.data.data[i].should_amount - res.data.data[i].paid_amount;
          }
          this.tablePayData = res.data.data;
        }
      });
    },
    returnIndex(){
      let url = this.$route.query.subPage ? this.$route.query.subPage : '/newStudent/studentIndex'
      this.$router.push({
        path: url,
        query: {
          activeType: this.$route.query.activeType,
          userType: this.loginUserAppType,
          navH: this.navHeight,
          appType: this.globalAppShow,
          sessionId: this.$route.query.sessionId
        }
      });
    },
    okOrder(){
      let params = {
        userId: this.loginUserId
      };
      params = this.$qs.stringify(params);
      this.$axios.post(common.enroll_checkin_student_print, params).then(res => {
        if (res.data.code == 200){
          let url = this.$route.query.subPage ? this.$route.query.subPage : '/newStudent/studentIndex'
          this.returnGIndex(url);
          MessageSuccess(res.data.desc);
        }else {
          MessageError(res.data.desc);
        }
      });
    }
  }
}
